#article-main-content * {
  font-family: 'Monaco', 'Lucida Console', monospace;
}

#article-content, .panel-body {
  word-break: break-all;
}

#article-content {
  background: #fff;
  img {
    width: 20%;
    border: 1px solid #b4b4b4;
  }
}

.reply {
  padding: 0 10px;
  margin: 0;
  height: 22px;
  line-height: 18px;
  font-size: 14px;
  float: right;
}

.comment-avatar {
  width: 25px;
  height: 25px;
  margin-right: 10px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
}

.reply-well {
  margin-top: -20px;
  .reply-list {
    p {
      margin: 0;
    }
  }
}

.content-header-avatar {
  height: 50px;
  width: 50px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  margin-right: 10px;
}

.comment-title {
  font-size: 1.2em;
}

.comment-content {
  word-break: break-all;
}